.body 
{
    margin:auto;
}
.Rectangle-3 {

    width: 87.5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left:6.25%;
    margin-right:6.25%;
    
  }
  .Div1 {
    width: 100%;
    height: 200px;
    background-image: url("background.jpg"); 
    background-repeat: no-repeat;
    background-size: 100% 100%;   
  }


  .Div2 {
    display: inline-block;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 6vh;
    margin-top:1.3%;
    -ms-flex-direction: row;
        flex-direction: row;
  
  }
  .Search
{
    width:100%;
    height: 100%;   
}
  .Div3 {
    
    width: 100%;
    height: 100%;
    margin-top: 1.3%;
    display: grid;
    grid-column-gap: 2%;
    grid-row-gap:20px;
    grid-template-columns: 32% 32% 32%;
    
  }

.warning
{
  margin-top:10%;
}
@import url('https://fonts.googleapis.com/css?family=Muli:400');


